<template>
  <div>
    <div class="mt-2 layout">
      <div class="row mb-4">
        <div class="col-6">
          <img class="img img-fluid" src="../assets/download.png" alt="logo" />
        </div>
        <div class="col-6 mt-4 mt-lg-5">
          <h4>Full Loading Spinner</h4>
          <p class="text-muted pt-2">
            A Vue Component
          </p>
        </div>
      </div>
      <div class="border mb-5 py-3 px-3 rounded">
        <div class="font-weight-bold d-flex justify-content-between mb-lg-2">
          <p>&lt;&gt;</p>
          <p>Example</p>
        </div>
        <form action="">
          <div class="row">
            <div class="col-12 col-lg-12 d-lg-flex">
              <div class="form-group d-lg-flex">
                <div class="text-center">
                  <label class="font-weight-bold mt-lg-2" for="">Label</label>
                </div>
                <input
                  class="form-control ml-lg-3"
                  type="text"
                  v-model="Loading"
                />
              </div>
              <div class="form-group d-lg-flex">
                <div class="text-center">
                  <label class="font-weight-bold ml-lg-4 mt-lg-2" for=""
                    >TimeOut</label
                  >
                </div>
                <input
                  class="form-control ml-lg-3"
                  type="text"
                  v-model="Timeout"
                />
              </div>
            </div>
          </div>
          <div class="text-center">
            <button
              v-on:click.prevent="setTimer"
              class="bg-info border w-50 borderedless text-light py-1 rounded mt-lg-3"
            >
              Show loader
            </button>
          </div>
        </form>
        <div
          v-if="msgload"
          class="msg text-center bg-info font-weight-bold mt-3 rounded text-light"
        >
          {{ msg }}
        </div>
      </div>
    </div>
    <div class="foot text-right pb-5">
      <Footer />
      <div v-show="loader" class="pt-1 mr-3 mr-lg-5 pr-lg-5">
        <span>{{ Loading }}</span>
        <div class="spinner-border ml-3 text-primary" role="status">
          <span class="sr-only">Loading...</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import Footer from "@/components/Footer.vue";
export default {
  name: "Home",
  components: {
    Footer,
  },
  data() {
    return {
      Loading: "Loading...",
      Timeout: 2000,
      loader: "",
      msg: "I'm Done Loading, Please Reload",
      msgload: false,
    };
  },
  methods: {
    setTimer() {
      this.loader = true;
      this.msgload = "";
      setTimeout(() => {
        this.msgload = true;
        this.loader = false;
      }, this.Timeout);
    },
  },
};
</script>

<style scoped>
.layout {
  width: 90%;
  margin: auto;
}
.text-muted {
  border-top: 1px dashed;
}
.msg {
  background: rgb(41, 41, 82);
}
.foot {
  background: rgb(231, 229, 229);
}
@media screen and (min-width: 500px) {
  .layout {
    width: 50%;
  }
}
</style>
